<template>
  <div class="mainwrap mainXiaoGuo">
    <section class="head">
      <template v-if="!editStatus">
        <div>
          <a @click="goBack" class="iconfont back"> &#xe614; </a>
          <a
            @click="bus.$emit('showDrawer')"
            class="icon icon_manue"
            v-if="!cartEmpty"
          >
            manue
          </a>
        </div>
      </template>
      <template v-if="editStatus && !cartEmpty">
        <div class="headRadio" @click="refreshCheckAll">
          <span class="iconfont" :class="{ red: radioAllValue }">
            {{ radioAllValue ? "&#xe63a;" : "&#xe608;" }}
          </span>
        </div>
      </template>

      <h1>Cart</h1>
      <div v-if="cartEmpty">&nbsp;</div>
      <div class="edit" v-if="!cartEmpty" @click="editStatus = !editStatus">
        {{ editStatus ? "Cancel" : "Edit" }}
      </div>
    </section>

    <template v-if="!cartEmpty">
      <section class="cartList">
        <ul class="clearfix">
          <li
            v-for="(item, index) in initProData"
            v-if="!!item.settlementSku"
            @click="$router.push({path:'/proDetail',query:{spuCode:item.settlementSku.spuCode}})"
            :key="index"
          >
            <van-swipe-cell :right-width="operateW">
              <div class="item">
                <div
                  class="radio"
                  @click.stop="checkPro(index, item.id)"
                  :data-check="item.checked"
                >
                  <span class="iconfont" :class="{ red: item.checked }">
                    {{ item.checked ? "&#xe63a;" : "&#xe608;" }}
                  </span>
                </div>
                <div class="imgWrap">
                  <img class="img" :src="item.settlementSku.img0" />
                  <!-- <span class="leftNum" :class="{'soldOut':!item.leftNum}"> {{!item.leftNum?"Sold out":(item.leftNum+"Left")}} </span> -->
                </div>
                <div class="rightMain">
                  <div class="name">{{ item.settlementSku.name }}</div>
                  <div class="sizeWrap">
                    <div>
                      <p class="cartChooseSize" @click.stop="updateItem(item)">
                        <span>
                          {{ getAttributes(item.settlementSku.attributeList) }}
                        </span>
                        <span class="iconfont"> &#xe671; </span>
                      </p>
                    </div>
                  </div>
                  <div class="cartDownWrap">
                    <div class="bottomPrice">
                      <p class="red">
                        {{ (item.settlementSku.mallPrice / 100).toFixed(2) }}
                        AED
                      </p>
                      <!-- supplyPrice -->
                      <p class="gray">
                        {{ (item.settlementSku.price / 100).toFixed(2) }} AED
                      </p>
                    </div>
                    <div class="downNum">
                      <template v-if="item.settlementSku.stock > 0">
                        <!--有库存 item.productSku.stock-->
                        <p
                          class="free"
                          v-if="item.settlementSku.isTypeFour == 1"
                        >
                          Free Shipping
                        </p>
                        <div class="gw_num clearfix">
                          <em
                            class="jian iconfont"
                            @click.stop="
                              jian(
                                item.id,
                                item.num,
                                item.settlementSku.stock,
                                item.skuCode
                              )
                            "
                          >
                            -
                          </em>
                          <input
                            type="number"
                            v-model="item.num"
                            class="num"
                            @blur="
                              selectNum(
                                item.id,
                                item.num,
                                item.settlementSku.stock,
                                item.skuCode
                              )
                            "
                          />
                          <em
                            class="add iconfont"
                            @click.stop="
                              jia(
                                item.id,
                                item.num,
                                item.settlementSku.stock,
                                item.skuCode
                              )
                            "
                          >
                           +
                          </em>
                        </div>
                      </template>
                      <template v-if="item.settlementSku.stock == 0">
                        <!--售罄-->
                        <div
                          class="findSame"
                          @click="
                            $router.push({
                              path: 'searchResult',
                              query: { category2Code: '' }
                            })
                          "
                        >
                          Find Similarity
                        </div>
                      </template>
                    </div>
                  </div>
                </div>
              </div>
              <div slot="right" class="slideOperate">
                <div class="share" @click="shareShadow(item)">
                  <p class="iconfontwrap"> <span class="iconfont"> &#xe650; </span> </p>
                  <span> Share </span>
                </div>
                <div class="like">
                  <p class="iconfontwrap"> <span class="iconfont" @click="moveToWishList(item)">
                    &#xe652;
                  </span></p>
                  <span>
                    Move to <br />
                    Wishlist
                  </span>
                </div>
                <div class="del" @click="delPro(item.id)">
                  <p class="iconfontwrap"><span class="iconfont"> &#xe609; </span></p>
                  <span>Delete</span>
                </div>
              </div>
            </van-swipe-cell>
          </li>
        </ul>
      </section>

      <section class="salesWrap" >
        <div class="box box-flex justify-between" v-if="recommendList.length>0">
          <div class="timerWrap">
            <h3 class="homeTittle">Recommend for You</h3>
          </div>
          <div>
            <router-link :to="{ path: '/categoryElectronics', query: {} }">
              <a class="blue"> See All</a>
            </router-link>
          </div>
        </div>
        <div class="commonPro">
          <swiper :options="commonSwiperOption" v-if="recommendList.length>0">
            <template v-for="(item, index) in recommendList" >
              <swiper-slide>
                <div class="item" @click="$router.push({path:'/proDetail',query:{spuCode:item.spuCode}})">
                  <div class="tips_cutOff" v-if="item.percentage !='0%'">
                    {{ item.percentage }} OFF
                  </div>
                  <div
                    class="tips_love iconfont"
                    @click.stop="handlerLove(item)"
                    :class="{ active: wishCode.indexOf(item.spuCode) > -1 }"
                  >
                    {{
                      wishCode.indexOf(item.spuCode) > -1
                        ? "&#xe653;"
                        : "&#xe652;"
                    }}
                  </div>
                  <div class="tips_sale" v-if="item.sale"></div>
                  <img class="img" :src="item.spuMainImage" alt="" />
                </div>
                <div class="name" @click="$router.push({path:'/proDetail',query:{spuCode:item.spuCode}})">{{ item.name }}</div>
                <div class="bottomPrice" @click="$router.push({path:'/proDetail',query:{spuCode:item.spuCode}})">
                  <p class="red">{{ getStr(item.priceRange) }} AED</p>
                  <p class="gray">{{ getStr(item.priceRangeLine) }} AED</p>
                  <p v-if="item.isTypeFour == 1">Free Shipping</p>
                </div>
                <div class="downShare">
                  <div
                    :class="{ active: item.share }"
                    @click.stop="shareShadow(item)"
                  >
                    <span class="share iconfont"> &#xe650; </span>
                  </div>
                  <div :class="{ active: item.cart }" @click.stop="addChart(item)">
                    <span class="cart iconfont"> &#xe669; </span>
                  </div>
                </div>
              </swiper-slide>
            </template>
          </swiper>
        </div>

        <div class="box mt10 box-flex justify-between" v-if="browserList.length>0">
          <div class="timerWrap">
            <h3 class="homeTittle">Browse Record</h3>
          </div>
          <div>
            <router-link :to="{ path: '/browseRecord', query: {} }">
              <a class="blue"> See All</a>
            </router-link>
          </div>
        </div>
        <div class="commonPro" v-if="browserList.length>0">
          <swiper :options="commonSwiperOption">
            <template v-for="(item, index) in browserList" >
              <swiper-slide>
                <div class="item"  @click="$router.push({path:'/proDetail',query:{spuCode:item.spuCode}})">
                  <div class="tips_cutOff" v-if="item.percentage !='0%'">
                    {{ item.percentage }} OFF
                  </div>
                  <div
                    class="tips_love iconfont"
                    @click.stop="handlerLove(item)"
                    :class="{ active: wishCode.indexOf(item.spuCode) > -1 }"
                  >
                    {{
                      wishCode.indexOf(item.spuCode) > -1
                        ? "&#xe653;"
                        : "&#xe652;"
                    }}
                  </div>
                  <div class="tips_sale" v-if="item.sale"></div>
                  <img class="img" :src="item.spuMainImage" alt="" />
                </div>
                <div class="name" @click="$router.push({path:'/proDetail',query:{spuCode:item.spuCode}})">{{ item.name }}</div>
                <div class="bottomPrice" @click="$router.push({path:'/proDetail',query:{spuCode:item.spuCode}})">
                  <p class="red">{{ getStr(item.priceRange) }} AED</p>
                  <p class="gray">{{ getStr(item.priceRangeLine) }} AED</p>
                  <p v-if="item.isTypeFour == 1">Free Shipping</p>
                </div>
                <div class="downShare">
                  <div
                    :class="{ active: item.share }"
                    @click.stop="shareShadow(item)"
                  >
                    <span class="share iconfont"> &#xe650; </span>
                  </div>
                  <div :class="{ active: item.cart }" @click.stop="addChart(item)">
                    <span class="cart iconfont"> &#xe669; </span>
                  </div>
                </div>
              </swiper-slide>
            </template>
          </swiper>
        </div>
      </section>

      <section v-if="!editStatus" class="downWrap">
        <div class="tips" v-if="cartData.message">
          <div class="red">{{ cartData.message }}</div>
          <div class="gray" v-if="true">
            To gather up a list <span class="iconfont"> &#xe93d; </span>
          </div>
        </div>
        <div class="downBtn">
          <div class="leftAll" @click="refreshCheckAll">
            <span class="iconfont" :class="{ red: radioAllValue }">
              {{ radioAllValue ? "&#xe63a;" : "&#xe608;" }}
            </span>
            <span> All </span>
          </div>
          <div class="rightOperate">
            <span class="gray">Sub Total:</span>
            <span class="price"> {{ totalPrice }} AED </span>
            <van-button type="danger" @click="orderSummary"
              >Check out({{ selectSpuNum }})</van-button
            >
          </div>
        </div>
      </section>

      <section
        v-if="editStatus"
        class="editDownOperate clearfix"
        @click="editStatus = false"
      >
        <div class="col">
          <span class="iconfont"> &#xe652; </span>
          <span class="font10" @click="moveToWish"> Move to Wishlist </span>
        </div>
        <div class="col" @click="shareList">
          <div>
            <span class="iconfont"> &#xe650; </span>
          </div>
          <span class="font10">Share </span>
        </div>
        <div class="col">
          <span class="iconfont"> &#xe609; </span>
          <span class="font10" @click="deleteCart"> Delete </span>
        </div>
      </section>
    </template>

    <template v-if="cartEmpty">
      <section class="cartEmpty">
        <img src="../common/images/cartEmpty.png" />
        <p class="gray">Your Cart is empty</p>
        <p class="btnCartWrap">
          <van-button
            type="danger"
            size="large"
            @click="$router.push('flashSale')"
            >EXPLORE OUR DEALS</van-button
          >
        </p>
      </section>
    </template>

    <!-- 添加购物车 -->
    <add-cart ref="addCart" v-on:reloadCart="init"> </add-cart>
  </div>
</template>

<script>
import { myCarts, checkCart, deleteCart, updateCart } from "@/api/cart.js";
import { getUserBrowseProduct } from "@/api/user";
import { mapGetters } from "vuex";
export default {
  name: "cart",
  data() {
    return {
      totalPreferentialPrice: null,
      totalPrice: "",
      cartEmpty: false,
      selectSpuNum: 0,
      commonSwiperOption: {
        slidesPerView: 2.3,
        spaceBetween: 10,
        freeMode: true,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      radioAllValue: 0,
      operateW: 192,
      editStatus: false,
      cartList: [],
      initProData: [],
      recommendList: [],
      browserList: [],
      cartData: {}
    };
  },
  computed: {
    ...mapGetters(["wishCode", "wishes", "carts"])
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.bus.$emit("loadIndexData");
      this.$store
        .dispatch("myCarts")
        .then(carts => {
          this.radioAllValue = false;
          this.refreshCheckAll();
//          console.log(carts);
          if (!!carts.cartProductList && carts.cartProductList.length > 0) {
            this.cartData = carts;
            this.initProData = carts.cartProductList.filter(val => {
              return !!val.settlementSku;
            });
            this.recommendList = carts.cartRecommendList;
            this.totalPreferentialPrice = carts.totalPreferentialPrice;
            this.totalPrice = (carts.totalPrice / 100).toFixed(2);
             this.cartEmpty=false;
          } else {
            this.cartEmpty = true;
          }
        })
        .catch(e => {
          this.cartEmpty = true;
        });
        this.$store.dispatch('getUserBrowseProduct',{ pageNo: 1, pageSize: 8 }).then(res=>{
           this.browserList = res;
        });

    },
    initCartCheck() {
      this.proDataBest.map(val => {
        this.initProData.push(
          JSON.parse(JSON.stringify(Object.assign(val, { check: true })))
        );
      });
    },
    //勾选商品
    checkPro(idx, id, checked) {
      this.initProData[idx].checked = !this.initProData[idx].checked;
      this.checkAll();
      if (checked) {
        checkCart(id).then(res => {});
      }
    },
    refreshCheckAll() {
      this.radioAllValue = !this.radioAllValue;
      let price = 0;
      this.initProData.map(val => {
        val.checked = this.radioAllValue;
        if (val.checked) {
          if (!!val.settlementSku) {
            price += val.settlementSku.price * val.num;
          }
        }
      });
      this.totalPrice = price == 0 ? "0.00" : (price / 100).toFixed(2);
      this.checkAll();
    },
    checkAll() {
      //检查是否全部选中
      let checkedList = this.initProData.filter(val => {
        return val.checked;
      });
      let price = 0;
      checkedList.forEach(e => {
        if (!!e.settlementSku) {
          price += e.settlementSku.price * e.num;
        }
      });
      this.totalPrice = price == 0 ? "0.00" : (price / 100).toFixed(2);
      let checkNum = checkedList.length;
      if (checkNum < this.initProData.length) {
        this.radioAllValue = 0;
      } else {
        this.radioAllValue = 1;
      }
      this.selectSpuNum = checkNum;
    },
    // 删除商品
    delPro(id) {
      // this.initProData.splice(idx,1)
      this.$store.dispatch("deleteCart", { id }).then(res => {
        this.$toast("Delete Successfully");
        this.init();
      });
    },
    updateItem(item) {
      this.$refs.addCart.update(item);
    },
    //修改商品
    jian(id, num, stock, skuCode) {
      num--;
      if (num <= 1) {
        num = 1;
      }
      this.$store.dispatch("updateCart", { id, num, skuCode }).then(res => {
        this.init();
      });
      this.checkAll();
    },
    jia(id, num, stock, skuCode) {
      num++;
      if (num > stock) {
        this.$toast("Out of Stock");
        num = stock;
      }
      this.$store.dispatch("updateCart", { id, num, skuCode }).then(res => {
        this.init();
      });
      this.checkAll();
    },
    selectNum(id, num, stock, skuCode) {
      if (num > stock) {
        this.$toast("Out of Stock");
        num = stock;
      }
      this.$store.dispatch("updateCart", { id, num, skuCode }).then(res => {
        this.init();
      });
    },
    goBack() {
      this.$router.back(-1);
    },
    //跳到确认订单页面
    orderSummary() {
      if(!this.$store.getters.token){
          this.$router.push({path:'/login',query:{from:'/cart'}});
      }else{
        let ids = [];
        this.initProData.map(item => {
          if (item.checked && item.settlementSku.stock > 0) {
            ids.push(item.id);
          }
        });
        this.$router.push({
          path: "orderSummary",
          query: { ids: JSON.stringify(ids) }
        });
      }
    },
    getAttributes(arr) {
      return arr.map(t => t.optionName).join(";");
    },
    handlerLove(item) {
      let index = this.wishCode.indexOf(item.spuCode);
      let isAdd = index == -1;
      this.$store
        .dispatch(
          isAdd ? "addWish" : "deleteWish",
          isAdd
            ? { type: "spu", code: item.spuCode }
            : { id: this.wishes.result[index].id }
        )
        .then(res => {
          if(isAdd){
            this.$toast("Move to Whislist Successfully");
          }else{
            this.$toast("Remove From Whislist Successfully");
          }
          this.$store.dispatch("myWishes");
        });
    },
    // 弹出规格
    addChart(data) {
      this.$refs.addCart.load(data);
    },
    moveToWishList(item) {
      this.$store
        .dispatch("addWish", { type: "sku", code: item.skuCode })
        .then(res => {
          this.delPro(item.id);
          this.$toast(
            "Move to Whishlist Successfully. You can check it in Whishlist"
          );
          this.$store.dispatch("myWishes");
        });
    },
    moveToWish() {
      this.initProData.forEach(e => {
          if(e.checked){
            this.$store
              .dispatch("addWish", { type: "sku", code: e.skuCode })
              .then(res => {
                this.delPro(e.id);
                this.$store.dispatch("myWishes");
              });
          }
      });
      this.editStatus = false;
    },
    deleteCart() {
      this.initProData.forEach(e => {
        if(e.checked){
          this.delPro(e.id);
        }
      });
      this.editStatus = false;
    },
    shareShadow(item) {
      if (!item) {
        item = this.productDetail;
      }
      console.log(item);
      if (item.settlementSku) {
        this.$store
          .dispatch("share", {
            imageUrl: [item.settlementSku.img0],
            url: "proDetail?spuCode=" + item.settlementSku.spuCode,
            title: item.settlementSku.name
          })
          .then(res => {});
      } else {
        this.$store
          .dispatch("share", {
            imageUrl: [item.spuMainImage],
            url: "proDetail?spuCode=" + item.spuCode,
            title: item.name
          })
          .then(res => {});
      }
    },
    getStr(obj) {
      var index = obj.indexOf("-");
      if (index != -1) {
        var str = obj.substring(0, index - 1);
        return str;
      } else {
        return obj;
      }
    },
    shareList() {
      let spuCodes = [],
        img;
      this.initProData.forEach(e => {
        if (e.checked) {
          if (!img) {
            img = e.settlementSku.img0;
          }
          spuCodes.push(e.settlementSku.spuCode);
        }
      });
      let url;
      if (spuCodes.length > 0) {
        if (spuCodes.length > 1) {
          url = "shareList?spuCodes=" + spuCodes.join(",");
        } else {
          url = "proDetail?spuCode=" + spuCodes[0];
        }
        this.$store.dispatch("share", {
          imageUrl: [img],
          url: url,
          title: "我在 分享了商品给你"
        });
      }
      this.editStatus = false;
    }
  },
  watch: {
    carts: function(carts) {
      if (carts) {
        this.cartData = carts;
        this.initProData = carts.cartProductList.filter(val => {
          return !!val.settlementSku;
        });
        this.recommendList = carts.cartRecommendList;
        this.totalPreferentialPrice = carts.totalPreferentialPrice;
        this.totalPrice = (carts.totalPrice / 100).toFixed(2);
        this.cartEmpty=false;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../common/css/cart.scss";
</style>



